.footer {
  min-height: 100px;
  background: #E5E5E5;
  position: relative;
}

.footer span {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 3px;
  background: #B8B8B8;
}

#sticky-footer {
  bottom          : 0;
  width           : 100%;
  position        : fixed;
  background-color: #8bacbd;
}